
import { defineComponent, computed } from 'vue'

export default defineComponent({
  name: "Memo",
  props: {
    modelValue: String,
  },
  setup(props, { emit }) {
    const textarea = computed({
      get: () => props.modelValue,
      set: (value) => emit("update:modelValue", value),
    });
    return { textarea }
  }
})
